<template>
  <div class='mx-4 mx-md-3 mx-lg-2 mx-xl-1'>
    <div class='mt-3 card-columns'>
      <FilmCard v-for='film in films'
                :key='film.id'
                :film='film'
                :liked="checkStatus('liked', film.id)"
                :disliked="checkStatus('disliked', film.id)"
                :seen="checkStatus('seen', film.id)"
      />
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import FilmCard from './FilmCard.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'ListFilms',
  components: {
    FilmCard
  },
  data () {
    return {
      films: [],
      likedFilms: [],
      dislikedFilms: [],
      seenFilms: [],
      likesId: ''
    }
  },
  created () {
    axios
      .get(`${this.$config.API}/film`, {
        headers: {
          Authorization: `Bearer ${this.$store.state.token}`
        }
      })
      .then(response => {
        if (!response.data) {
          throw response
        }
        this.films = response.data.data
        axios
          .get(`${this.$config.API}/like?user._id=${this.loggedInUserId}`, {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`
            }
          })
          .then(response => {
            /* I'm gonna do something ugly. I'm going through each film fetched above, and
            then looking for an item in the current response with a matching ID. If found,
            I'm adding a key to the film object in local state. I'm doing this because...
            Well, it feels like the best way to do it, but this can probably be refactored
            quite well. */
            this.likedFilms = response.data[0].likes
            this.dislikedFilms = response.data[0].dislikes
            this.seenFilms = response.data[0].seen
            this.likesId = response.data[0].id
          })
          .catch(err => console.error(err))
      })
      .catch(err => console.error(err))
  },
  computed: {
    ...mapGetters({
      loggedInUser: 'getUsername',
      loggedInUserId: 'getUserId'
    })
  },
  methods: {
    checkStatus: function (state, filmId) {
      return (
        this[state + 'Films'].findIndex(el => {
          console.log(`Looking for ${filmId}, found ${el.id}`)
          return el.id === filmId
        }) > -1
      )
    }
  }
}
</script>

