<template>
  <div class="mx-4 mx-md-3 mx-lg-2 mx-xl-1">
    <h2>Liked Films</h2>
    <ul class="mt-3">
      <li v-for="film in likedFilms" :key="film.id">{{ film.Title }} <delete-icon @click="unlike" :id="film.id" style="height: 1em" /></li>
    </ul>
    <h2>Disliked Films</h2>
    <ul class="mt-3">
      <li v-for="film in dislikedFilms" :key="film.id">{{ film.Title }} <delete-icon @click="undislike" :id="film.id" style="height: 1em" /></li>
    </ul>
    <h2>Seen Films</h2>
    <ul class="mt-3">
      <li v-for="film in seenFilms" :key="film.id">{{ film.Title }} <delete-icon @click="unsee" :id="film.id" style="height: 1em" /></li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
import { mapGetters } from 'vuex'
import { DeleteIcon } from 'vue-feather-icons'

export default {
  name: 'LikedFilms',
  data () {
    return {
      likedFilms: [],
      dislikedFilms: [],
      seenFilms: [],
      likesId: ''
    }
  },
  created () {
    axios
      .get(`${this.$config.API}/like?user._id=${this.loggedInUserId}`, {
        'headers': {
          'Authorization': `Bearer ${this.$store.state.token}`
        }
      })
      .then(response => {
        this.likedFilms = response.data[0].likes
        this.dislikedFilms = response.data[0].dislikes
        this.seenFilms = response.data[0].seen
        this.likesId = response.data[0].id
      })
      .catch(err => console.error(err))
  },
  computed: {
    ...mapGetters({
      loggedInUser: 'getUsername',
      loggedInUserId: 'getUserId'
    })
  },
  methods: {
    unlike (e) {
      const filmId = e.target.id
      const newFilms = this.likedFilms.filter((film) => film.id !== filmId)
      axios.put(`${this.$config.API}/like/${this.likesId}`, {
        likes: newFilms
      }, {
        headers: {
          Authorization: `Bearer ${this.$store.state.token}`
        }
      })
        .then(() => {
          this.likedFilms = newFilms
        })
        .catch((err) => console.error(err))
    },
    undislike (e) {
      const filmId = e.target.id
      const newFilms = this.dislikedFilms.filter((film) => film.id !== filmId)
      axios.post(`${this.$config.API}/like?id=${this.likesId}`, {
        dislikes: newFilms
      })
        .then((response) => {
          this.dislikedFilms = newFilms
        })
        .catch((err) => console.error(err))
    },
    unsee (e) {
      const filmId = e.target.id
      const newFilms = this.seenFilms.filter((film) => film.id !== filmId)
      axios.post(`${this.$config.API}/like?id=${this.likesId}`, {
        seen: newFilms
      })
        .then((response) => {
          this.seenFilms = newFilms
        })
        .catch((err) => console.error(err))
    }
  },
  components: {
    DeleteIcon
  }
}
</script>

